body{justify-content:space-between;background:#e5e5e5;font-size:.9rem;line-height:1.5;font-family:-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;color:#333}.showcase__wrapper,body{display:flex;flex-direction:column}.showcase__wrapper{align-items:center;min-width:980px;max-width:1600px;margin:0 auto;padding:1rem 2rem}.showcase__link,.showcase__subtitle,.showcase__title{text-align:center}.showcase__title{font-size:1.5rem;line-height:1.4;font-weight:700}.showcase__subtitle{margin-bottom:.25rem;font-size:1rem;line-height:1.4;letter-spacing:.2em}.showcase__link{margin-bottom:1rem;font-weight:700;color:#2e8b57;text-decoration:underline;text-decoration-skip:edges}.showcase__link:hover{opacity:.7;transition:all .2s ease-in-out}.showcase__sample{width:100%;margin-bottom:1rem}.sample{display:flex;justify-content:space-between;width:100%;min-height:10rem;box-shadow:0 0 10px #ccc;border:1px solid #ccc;border-radius:.5rem;padding:1rem;background:#fff}.sample__demo{align-self:center;width:60%}.sample__settings{align-self:flex-start;width:calc(40% - 2rem)}.sample__setting{display:flex;flex-direction:column;margin-bottom:.5rem}.sample__setting-title{font-size:.8rem;font-weight:700;text-transform:uppercase}.sample__setting-field{border:1px solid #ccc;border-radius:3px;padding:.2rem .5rem}